import React, {Component} from 'react';
import $ from 'jquery';
import axios from 'axios';
import cookie from 'react-cookies';
import {Redirect} from 'react-router';
import Navbarheader from './Navbarheader';
import Popup from "reactjs-popup";
import md5 from 'md5';

class Userlist extends Component {
    constructor(){
        super();
        this.state = { 
            userlist : [],
            userRollList:[]
        }
        this.updateUser = this.updateUser.bind(this);
        this.addUser = this.addUser.bind(this);
        this.checkUser = this.checkUser.bind(this);
    }
    componentDidMount(){
        axios.get('http://localhost:5001/userlist')
            .then((response) => {
            response.data.map((val, i) => {     
                if(val.status)
                    response.data[i].status = "Active"
                else
                    response.data[i].status = "In-Active"
            })
            this.setState({
                userlist : this.state.userlist.concat(response.data) 
            })
        })
        axios.get('http://localhost:5001/userrolllist')
            .then((response) => {
            this.setState({
                userRollList : this.state.userRollList.concat(response.data) 
            })
        })
    }
    state = { show: false };

    showModal = () => {
    this.setState({ show: true });
    };

    hideModal = () => {
    this.setState({ show: false });
    };

    logout = () => {
        cookie.remove('cookie', {path: '/'})
        console.log("Cookie removed!")
        window.location = "/"
    }
    handleValidation(editVendorID){
        let redirectVar = null;
         if(!cookie.load('cookie')){
            return redirectVar = <Redirect to= "/" />
        }
        let formIsValid = true;
        if($("#editVendorName-"+editVendorID).val() ==''){
           formIsValid = false;
           alert("Vendor name Required");
        }
        if($("#editVendorGSTIN-"+editVendorID).val() ==''){
           formIsValid = false;
           alert("Vendor GSTIN Required");
        }
        return formIsValid;
    }
    updateUser = (e, editVendorID) => {
        if(this.handleValidation(editVendorID)){
            var veditStatus = 0; 
            if($("#editVendorStatus1-"+editVendorID).is(':checked'))    veditStatus =1;
             
            const paramdata ={
                vendorName:$("#editVendorName-"+editVendorID).val(),
                vendorGSTIN:$("#editVendorGSTIN-"+editVendorID).val(),
                vendorstatus:veditStatus,
                vendorExpenseType:$("#editVendorExpenseType-"+editVendorID).val(),
                vendorId:$("#editVendorId-"+editVendorID).val()
            }
            e.preventDefault();
           
            axios.defaults.withCredentials = true;
            axios.post('http://localhost:5001/editVendor',paramdata)
            .then(response => {
                if(response.status === 200){
                    this.setState({userlist: []});
                    response.data.map((val, i) => {     
                        if(val.status)
                            response.data[i].status = "Active"
                        else
                            response.data[i].status = "In-Active"
                    })
                    this.setState({
                        userlist : this.state.userlist.concat(response.data)
                    })
                }
            })
            .catch(error => {
                console.log("In error");
                console.log(error);
                alert("Deleting User Unsuccessful :(");
            })
        }
    }
    handleValidationAddUser(){
        let redirectVar = null;
         if(!cookie.load('cookie')){
            return redirectVar = <Redirect to= "/" />
        }
        let formIsValid = true;
        if($("#adduName").val() ==''){
           formIsValid = false;
           alert("User name is Required");
        }
        if($("#addName").val() ==''){
           formIsValid = false;
           alert("Name is Required");
        }
        if($("#addEmail").val() ==''){
           formIsValid = false;
           alert("Email is Required");
        }
        var email = $("#addEmail").val();
        var filter = /^([\w-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/;
        if (!filter.test(email)) {
            formIsValid = false;
            alert("Invalid Email!");
        }

        if($("#addUserRoll").val() == 0){
           formIsValid = false;
           alert("Select User Roll");
        }
        if($("#addPassword").val() ==''){
           formIsValid = false;
           alert("Password is Required");
        }
        if($("#addPassword").val() != $("#addConfirmPassword").val()){
           formIsValid = false;
           $("#addPassword").val('')
           $("#addConfirmPassword").val('')
           alert("Password Must be same");
        }
        return formIsValid;
    }
    checkUser = (e) =>{
        const paramdata ={
            userName:$("#adduName").val()
        }
        e.preventDefault();
        axios.defaults.withCredentials = true;
        axios.post('http://localhost:5001/checkUser',paramdata)
        .then(response => {
            if(response.status === 200){
                alert("Username allready exist!");
                //alert("Username allready exist!");
            }
        })
        .catch(error => {
            console.log("In error");
            console.log(error);
           
        })


    }
    addUser = (e) => {
        if(this.handleValidationAddUser()){
            var addStatus = 0; 
            if($("#addUserStatus1").is(':checked')) addStatus =1;
            //cookie.load('cookie')
            const paramdata ={
                userName:$("#adduName").val(),
                userFullName:$("#addName").val(),
                userEmail:$("#addEmail").val(),
                userRoll:$("#addUserRoll").val(),
                userPassword:md5($("#addPassword").val()),
                userStatus:addStatus,
                logId:cookie.load('uid')
            }
            e.preventDefault();
           
            axios.defaults.withCredentials = true;
            axios.post('http://localhost:5001/addUser',paramdata)
            .then(response => {
                if(response.status === 200){
                    this.setState({userlist: []});
                    response.data.map((val, i) => {     
                        if(val.status)
                            response.data[i].status = "Active"
                        else
                            response.data[i].status = "In-Active"
                    })
                    this.setState({
                        userlist : this.state.userlist.concat(response.data)
                    })
                    $(".popup-overlay").click();
                }
            })
            .catch(error => {
                console.log("In error");
                console.log(error);
                alert("Error in adding user :(");
            })
        }
    }

    
    render(){
        
        let details = this.state.userlist.map((userlist) => {
            return(
                <tr>
                    <td style={{outline: "thin solid"}}>{userlist.id}</td>
                    <td style={{outline: "thin solid"}} key={userlist.username}>{userlist.username}</td>
                    <td style={{outline: "thin solid"}} key={userlist.name}>{userlist.name}</td>
                    <td style={{outline: "thin solid"}}>{userlist.email}</td>
                    <td style={{outline: "thin solid"}}>{userlist.uRollName}</td>
                    <td style={{outline: "thin solid"}}>{userlist.createdUser}</td>
                    <td style={{outline: "thin solid"}}>{userlist.lastLogin}</td>
                    <td style={{outline: "thin solid"}}>{userlist.status}</td>
                    <td style={{outline: "thin solid"}}>
                        <Popup trigger={<button style={{margin: "2px"}} className="button btn btn-warning"> Edit </button>}  modal  closeOnDocumentClick >
                            <span> 
                                <h2 align="center"> User Edit :<h3 style={{color: "grey"}}>{userlist.vendorName}</h3></h2>
                                <br/>
                                <div class="vendor-edit-form">
                                    <input type="hidden" className="hidden" id={'editUserId-'+userlist.id} value={userlist.id} />
                                    <div class="form-group">
                                        <label>Vendor Name</label>
                                        <input id={'editVendorName-'+userlist.vid} type="text" class="form-control" name="vendorName" defaultValue={userlist.vendorName}></input>
                                    </div>
                                    <div class="form-group">
                                        <label>GSTIN</label>
                                        <input id={'editVendorGSTIN-'+userlist.vid} type="text" class="form-control" name="vendorGSTIN" defaultValue={userlist.GSTIN} ></input>
                                    </div>
                                    <div class="form-group">
                                        <label>Vendor Expense Type</label>
                                        <input id={'editVendorExpenseType-'+userlist.vid} type="text" class="form-control" name="vendorExpenseType" defaultValue={userlist.exp_type} ></input>
                                    </div>
                                    <div class="form-group">
                                        <label>Vendor Status</label><br/>
                                        <input type="radio" value="Active" id={'editVendorStatus1-'+userlist.vid} className={'editVendorStatus-'+userlist.vid} name={'editVendorStatus-'+userlist.vid} defaultChecked={userlist.vendorstatus ==="Active"} /> Active
                                        <input type="radio" value="In-Active" id={'editVendorStatus0-'+userlist.vid} className={'editVendorStatus-'+userlist.vid}  name={'editVendorStatus-'+userlist.vid} defaultChecked={userlist.vendorstatus ==="In-Active"}/> In-Active
                                    </div>
                                    <button onClick={(e) => this.updateUser(e, userlist.vid)} type="submit" class="btn btn-lg btn-success" style={{position: 'relative'}}>Update</button> 
                                </div>
                             </span>
                        </Popup>
                    </td>
                </tr>
            )
        })

        let userRollDropDown = this.state.userRollList.map((userRollList) => {
            return(
              <option value={userRollList.access}>{userRollList.userrollname}</option>
            )
        })
        
        return(
            <div>
                <Navbarheader />
                <div class="container">
                    <h3 align="center">User List</h3>
                    <ul class="nav nav-pills">
                    <h4>
                        <Popup trigger={<button style={{margin: "2px"}} className="button btn btn-primary">  Add new User </button>}  modal  closeOnDocumentClick >
                            <span> 
                                <h2 align="center">Add New User<h3 style={{color: "grey"}}></h3></h2>
                                <br/>
                                <div className="vendor-add-form">
                                    <div className="form-group">
                                        <label>User Name</label>
                                        <input id="adduName" onChange={(e) => this.checkUser(e)} type="text" class="form-control" name="adduName" defaultValue=""></input>
                                        <span id="userNameMessage" className="hidden" style={{color: "red"}}>*Username allready exist!</span>
                                    </div>
                                    <div className="form-group">
                                        <label>Name</label>
                                        <input id="addName" type="text" class="form-control" name="addName" defaultValue=""></input>
                                    </div>
                                    <div class="form-group">
                                        <label>Email</label>
                                        <input id="addEmail" type="email" className="form-control" name="addEmail" defaultValue="" ></input>
                                    </div>
                                    <div className="form-group">
                                        <label>User roll</label>
                                        <select id="addUserRoll" name="addUserRoll" className="form-control" >
                                            <option value="0">--Select--</option>
                                            {userRollDropDown} 
                                        </select>
                                    </div>
                                    <div class="form-group">
                                        <label>New Password</label>
                                        <input id="addPassword" type="password" className="form-control" name="addPassword" defaultValue="" ></input>
                                    </div>
                                    <div className="form-group">
                                        <label>Confirm Password</label>
                                        <input id="addConfirmPassword" type="text" className="form-control" name="addConfirmPassword" defaultValue="" ></input>
                                    </div>
                                    <div className="form-group">
                                        <label>Vendor Status</label>
                                        <br/>
                                        <input type="radio" value="Active" id="addUserStatus1" className="addUserStatus" name="addUserStatus1" defaultChecked="1" /> Active
                                        <input type="radio" value="In-Active" id="addUserStatus0" className="addUserStatus"  name="addUserStatus0" /> In-Active
                                    </div>
                                    <button onClick={(e) => this.addUser(e)} type="submit" className="btn btn-lg btn-success" style={{position: 'relative'}}>Update</button> 
                                </div>
                             </span>
                        </Popup>


                   </h4>



                    </ul>
                </div>
                
                <div class="container">
                    <table class="table" style={{outline: "thin solid"}} border="1px solid black">
                        <thead>
                            <tr style={{outline: "thin solid"}}>
                                <th bgcolor="grey">U. Id</th>
                                <th bgcolor="grey">Username</th>
                                <th bgcolor="grey">Name</th>
                                <th bgcolor="grey">email</th>
                                <th bgcolor="grey">User Roll</th>
                                <th bgcolor="grey">Created By</th>
                                <th bgcolor="grey">Last login</th>
                                <th bgcolor="grey">Status</th>
                                <th bgcolor="grey">Update</th>
                            </tr>
                        </thead>
                        <tbody>
                            {details}   
                        </tbody>
                    </table>   
                </div> 
            </div> 
        )
    }
}

 
export default Userlist;


